<template>
  <div id="mypage">
    <h1>마이 페이지</h1>
    <table>
      <tr>
        <td>이름</td>
        <td>{{user.user_name}}</td>
      </tr>
      <tr>
        <td>아이디</td>
        <td>{{user.user_id}}</td>
      </tr>
      <tr>
        <td>이메일</td>
        <td>{{user.user_email}}</td>
      </tr>
      <tr>
        <td>성별</td>
        <td v-if="user.user_gender">남</td>
        <td v-else>여</td>
      </tr>
      <tr>
        <td>연락처</td>
        <td>{{user.user_phone}}</td>
      </tr>
      <tr>
        <td colspan="2">
          <div class="centered" v-for="tour in tours" :key="tour.idPlan">
            <div style="border: 3px solid #4cbaa8;; border-radius: 10%; margin:10px; padding: 10px">
              <router-link :to="{ name: 'DetailTour', params: { id: tour.idPlan }}">
                {{tour.title}}
                <br><br>
                {{tour.depart_day}} ~ {{tour.arrive_day}}
                <br><br>
                {{tour.country_name}}
                <br><br>
              </router-link>
              <button @click="clickToggleShare($event, tour.idPlan)">{{shareButtonText(tour)}}</button>
            </div>
          </div>
        </td>
      </tr>
    </table>
    <router-link to='/'>Go To Main</router-link>
  </div>

</template>

<script>
import API from '../components/API'

export default {
  data () {
    return {
      user: this.$store.state.user,
      tours: ''
    }
  },
  methods: {
    clickToggleShare (event, id) {
      if (event.target.innerText === '공유하기') {
        event.target.innerHTML = '공유 취소하기'
      } else {
        event.target.innerText = '공유하기'
      }
      const data = {
        idPlan: id
      }
      API.toggleSharePlanAPI(this.$http, this.$env.apiUrl, data).catch(() => {})
    },
    shareButtonText (tour) {
      if (tour.is_shared === 0) {
        return '공유하기'
      } else {
        return '공유 취소하기'
      }
    }
  },
  created () {
    API.getMyPlanAPI(this.$http, this.$env.apiUrl).then(res => {
      this.tours = res.data
      console.log(this.tours)
    }).catch(err => {
      console.log(err)
    })
  },
  computed: {
  }
}
</script>
<style>
  table {
    margin: auto;
    text-align: center;
  }

  td {
    padding: 10px;
  }
</style>



